.navbar {
  background-color: #e4242e;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  top: 0;
  z-index: 90;
  width: 100%;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
}

.nav-logo {
  color: #f5b921;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  flex-grow: 1;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-menu li a {
  text-decoration:none;
  color:#46094f;
  font-weight: bold;
}

.nav-menu li a.nav-active{
  color: #fff;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #ffdd40;
}

.nav-item .nav-active {
  color: #ffdd40;
  
}

.nav-icon {
  display: none;
}



@media screen and (max-width: 560px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1pxsolid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #e4242e;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-item .active {
    color: #ffdd40;
    border: none;
  }
  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #ffdd40;
    width: 50px;
    height: 50px;
  }

  .nav-icon .fa-solid{
    width: 50px;
    height: 50px;
  }

  i.fa-times{
    background-image: url(../img/exit.png);
    background-size: 25px auto;
    width: 25px;
    height: 25px;
    z-index: 100;
    display: inline-block;
  }

  i.fa-bars{
    background-image: url(../img/images.png);
    background-size: 25px auto;
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    z-index: 100;
    display: inline-block;
  }
}